.contactForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 220px;
    padding: 20px;
    background-color: #f2f7f9;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  .formLabel {
    width: 100%;
    margin-bottom: 10px;
  }
  
  .formLabel input {
    float: right;
  }  